.App {
  text-align: center;
}

.clockContainer {
    margin: 1rem auto;

    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background-color: #282C34;
    border: 1rem solid #32363E;
    border-radius:50%;

    height: 18rem;
    width: 18rem;
}

.timeDisplay {
    color: #61DAFB;
    font-weight:700;
    font-size:42px;
}

.dateDisplay {
    color: #61DAFB;
    font-weight:300;
    font-size:13px;
}

.switch {
	position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.switch input {
	opacity:0;
    width:0;
    height:0;
}

.slider {
	position: absolute;
    cursor: pointer;
    background-color: #ccc;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: .4s;
    -webkit-transition: .4s;
}

.dateToggleContainer {
    display: inline-block;
    margin: 0 auto;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
    -webkit-transition: .4s;
}

input:checked + .slider {
  background-color: #61DAFB;
}

input:checked + .slider:before {
	transform: translateX(26px);
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

.slider.round {
	border-radius: 34px;
}

.slider.round:before {
	border-radius: 50%;
}
